@import 'vars';

.root {
    width: calc(100% + 32px);
    overflow: hidden;
    margin: 0 0 10px -16px;
    position: relative;
    font-size: 16px;
    font-weight: lighter;

    .item {
        text-align: center;
        display: block;
        flex-grow: 1;
        margin: 0 8px 0 0;
        user-select: none;

        &:last-of-type {
            margin: 0;
        }

        > * {
            overflow: hidden;
            padding: 7px 11px;
            display: block;
            position: relative;
            white-space: nowrap;
            background-color: #fff;
            border-radius: 20px;
            color: #000;
            transition: box-shadow 0.3s ease, transform 0.3s ease;

            @media all and (min-width: $tablet-landscape-breakpoint) {
                &:hover {
                    transform: translateY(-2px);
                    box-shadow: 0 8px 8px -6px rgba(0, 0, 0, 0.24);
                }
            }

            &:active {
                transform: none;
            }
        }

        &.active {
            //transform: translateY(-1px);

            > * {
                @media all and (min-width: $tablet-landscape-breakpoint) {
                    &:hover {
                        transform: none;
                        box-shadow: unset;
                    }
                }
            }
        }
    }
}

.list {
    width: 100%;
    display: flex;
    overflow: auto;
    list-style: none;
    margin: 0;
    padding: 6px 0;
    scroll-behavior: smooth;
    -webkit-overflow-scrolling: touch;
    font-weight: 500;
    font-size: 14px;

    &::before,
    &::after {
        content: '';
        display: block;
        padding: 4px 8px;
    }

    &.clipLeft {
        mask-image: linear-gradient(90deg, rgba(0,0,0,0) 30px, rgba(0,0,0,1) 50px);
    }

    &.clipRight {
        mask-image: linear-gradient(-90deg, rgba(0,0,0,0) 30px, rgba(0,0,0,1) 50px);
    }

    &.clipLeft.clipRight {
        mask-composite: intersect;
        mask-image: linear-gradient(90deg, rgba(0,0,0,0) 30px, rgba(0,0,0,1) 50px), linear-gradient(-90deg, rgba(0,0,0,0) 30px, rgba(0,0,0,1) 50px);
    }

    -ms-overflow-style: none;
    scrollbar-width: none;
}

.list::-webkit-scrollbar {
    width: 0;
    height: 0;
}
.list::-webkit-scrollbar-track {
    background: rgba(0, 0, 0, 0);
}
.list::-webkit-scrollbar-thumb {
    background: rgba(0, 0, 0, 0);
}

.controlContainerLeft,
.controlContainerRight {
    height: 100%;
    width: 44px;
    position: absolute;
    top: 0;
    z-index: 1;
    cursor: pointer;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    user-select: none;

    .control {
        width: 28px;
        height: 28px;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        font-weight: bold;
        transition: transform 0.3s ease;
    }
}

.controlContainerLeft {
    left: 0;

    .control {
        &:hover {
            transform: translateX(-3px);
        }
    }
}

.controlContainerRight {
    right: 0;

    .control {
        &:hover {
            transform: translateX(3px);
        }
    }
}
