@mixin button {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    flex: 0 0 auto;
    color: inherit;
    font-weight: inherit;
    font-size: inherit;
    cursor: pointer;
    background-color: transparent;
    border: 0 none;
    padding: 0;

    &:focus {
        outline: none;
    }

    &:disabled,
    [aria-disabled] {
        pointer-events: none;
    }
}

@mixin icon {
    display: block;
    flex: 0;

    > svg {
        display: block;
    }
}

@mixin startIcon {
    @include icon;
    margin-right: 8px;
}

@mixin endIcon {
    @include icon;
    margin-left: 8px;
}
