.item {
    display: block;
    cursor: pointer;

    .thumbnail {
        width: 100%;
        padding-top: 100%;
        position: relative;
        border-radius: 10px;
        overflow: hidden;

        > img {
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        > .emptyPlaceholder {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
        }
    }

    .meta {
        margin: 6px 0 0 0;
        padding: 0 6px;

        .title {
            font-size: 14px;
            font-weight: 600;
            line-height: 1.4;
            margin: 0;
            word-break: break-word;
        }

        .price {
            display: block;
            font-size: 12px;
            font-weight: 400;
            margin: 4px 0 0 0;

            > .discountPrice {
                margin: 0 6px 0 0;
            }

            > .lineThrough {
                text-decoration: line-through;
                opacity: 0.5;
            }
        }
    }

    &.list {
        display: flex;
        flex-wrap: wrap;
        width: 100%;

        .thumbnail {
            width: 90px;
            height: 90px;
            flex-shrink: 0;
            padding-top: 90px;
        }

        .meta {
            display: flex;
            align-items: center;
            width: calc(100% - 90px);
            padding: 0 0 0 16px;
            margin: 0;

            .title {
                font-size: 16px;
            }

            .price {
                font-size: 14px;
            }
        }
    }
}
