.servicesList {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    list-style: none;
    margin: 0;
    padding: 0;

    > li {
        width: calc(50% - 8px);
        margin: 0 0 16px 0;

        &:nth-last-of-type(2),
        &:last-of-type {
            margin: 0;
        }
    }

    &.list {
        display: block;

        > li {
            width: 100%;

            &:nth-last-of-type(2) {
                margin: 0 0 16px;
            }
        }
    }
}

.moreButtonContainer {
    width: 100%;
    display: flex;
    justify-content: center;
    margin: 16px 0 0 0;
}

.blurred {
    filter: blur(20px);
}
