@mixin crop($overflow: ellipsis) {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: $overflow;
}

@mixin cropLines($lines: 3, $overflow: ellipsis) {
    overflow: hidden;
    white-space: pre-wrap;
    text-overflow: $overflow;
    display: -webkit-box;
    -webkit-line-clamp: $lines;
    -webkit-box-orient: vertical;
}
