.buttonContainer {
    margin: 16px 0 0 0;
}

.successContent {
    padding: 32px 16px 32px 16px;
    position: relative;
}

.icon {
    display: block;
    margin: 0 auto 8px auto;
}

.closeIcon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 56px;
    height: 56px;
    position: absolute;
    top: 0;
    left: 0;
    cursor: pointer;
}

.text {
    margin: 0;
    padding: 0;
    font-size: 22px;
    font-weight: 600;
    text-align: center;
    white-space: pre-wrap;
}
