@import 'vars';

.root {
    .title {
        font-size: 110%;
        font-weight: bold;
        text-align: center;
        margin-bottom: 8px;
    }

    .list {
        list-style: none;
        margin: 0;
        padding: 0;
        display: flex;
        flex-wrap: wrap;
        gap: 16px;
        row-gap: 16px;
        column-gap: 16px;

        > li {
            width: calc(50% - 8px);
            border-radius: 10px;
            overflow: hidden;
            transition: box-shadow 0.3s ease, transform 0.3s ease, top 0.3s ease;
            transform: translate3d(0, 0, 0);
            user-select: none;
        }

        //@media all and (min-width: $tablet-portrait-breakpoint) {
        //    > li {
        //        width: calc(25% - 12px);
        //    }
        //}
    }

    &.mobileMode .list > li {
        width: calc(50% - 8px);
    }

    .empty {
        flex: 1;
        display: flex;
        flex-direction: column;

        .icon {
            margin: 0 auto;
        }

        .description {
            text-align: center;
        }
    }

    .moreBtnContainer {
        display: flex;
        justify-content: center;
        margin-top: 16px;

        .moreBtn {
            display: inline-flex;
            height: 32px;
            padding: 5px 24px;
            border-radius: 10px;
            font-size: 14px;
            line-height: 17px;
            letter-spacing: 0.02px;
            background-color: #fff;
            color: #000;
            margin: 0 auto;
            user-select: none;
            transition: transform 0.3s ease, box-shadow 0.3s ease;

            @media all and (min-width: $tablet-landscape-breakpoint) {
                &:hover {
                    box-shadow: 0 10px 10px -6px rgba(0, 0, 0, 0.24);
                    transform: translateY(-2px);
                }
            }
        }
    }
}
