.input {
    &::selection {
        background: currentColor;
    }

    &[type='date'],
    &[type='time'],
    &[type='datetime'],
    &[type='datetime-local'] {
        -webkit-appearance: none;
    }
}

.label {
    padding: 0 6px;
    margin: 0 0 6px 0;
    font-weight: 500;
    font-size: 16px;
    color: inherit;
}
