@import 'vars';
@import 'mixins/crop';

.root {
    position: relative;

    .select {
        position: relative;

        .button {
            overflow: hidden;
            margin: 0 0 8px 0;

            .label {
                flex: 1;
                padding: 4px 8px;
                font-weight: bold;
                font-size: 14px;
                line-height: 14px;
                max-width: 200px;
                @include crop();
            }

            .arrow {
                flex: 0 0 auto;
                margin-left: -4px;
                transition: transform 0.3s;
                transform: rotate(0deg);
            }

            &[aria-pressed='true'] .arrow {
                transform: rotate(180deg);
            }
        }

        .option {
            display: flex;
            flex-direction: row;
            padding: 8px;
            cursor: pointer;
            font-weight: normal;
            font-size: 14px;
            line-height: 16px;

            &:hover {
                background-color: rgba(0, 0, 0, 0.1);
            }

            &.active {
                font-weight: bold;
            }

            .optionLabel {
                flex: 1;
            }

            .badge {
                flex: 0;
                font-weight: lighter;
                background-color: #333;
                border-radius: 10px;
                color: #e2e2e2;
                padding: 0 8px;
            }
        }

        .option + .option {
            border-top: 1px solid $border-color;
        }
    }

    .dropdown {
        position: absolute;
        color: $font-default-color;
        background-color: $background-light;
        box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.1);
        border: 1px solid $border-color;
        border-radius: 4px;
        padding: 0;
        z-index: 2;
        width: 248px;
        max-height: 400px;
        overflow-y: auto;
        -webkit-overflow-scrolling: touch;
    }
}
