.pane {
    padding: 11px 16px;

    .radio {
        margin: 0 0 6px 0;
    }
}

.radio {
    margin: 0 0 10px 0;

    &:last-of-type {
        margin: 0;
    }

    .radioDot {
        transform: scale(0);
        transform-origin: 50% 50%;
        transition: transform 0.3s ease;
    }

    &.active .radioDot {
        transform: scale(1);
    }

    > label {
        display: inline-block;
        position: relative;
        font-size: 16px;
        line-height: 22px;
        padding: 0 0 0 28px;
        cursor: pointer;

        > svg {
            display: block;
            width: 18px;
            height: 18px;
            position: absolute;
            top: 2px;
            left: 0;
        }

        > input {
            display: none;
        }
    }
}
